import { DetailWrapper } from "./style";
import {
  Button,
  Avatar,
  Image,
  Comment,
  Divider,
  Form,
  Input,
  List,
  message,
} from "antd";
import React, { memo, useEffect, useState } from "react";
import { shallowEqual, useDispatch, useSelector } from "react-redux";
import { Link, useNavigate, useParams } from "react-router-dom";
import { getPost, comment } from "@/services/modules/home";
const { TextArea } = Input;

const Editor = ({ onChange, onSubmit, submitting, value }) => (
  <>
    <Form.Item>
      <TextArea
        showCount
        rows={4}
        maxLength={300}
        onChange={onChange}
        value={value}
      />
    </Form.Item>
    <Form.Item>
      <Button
        htmlType="submit"
        loading={submitting}
        onClick={onSubmit}
        type="primary"
      >
        Add Comment
      </Button>
    </Form.Item>
  </>
);

const CommentList = ({ comments, onChange }) => {
  const [replyId, setReplyId] = useState("");
  const commentsAfter = comments.map((item) => {
    return {
      ...item,
    };
  });
  return (
    <List
      locale={{ emptyText: "No comments" }}
      dataSource={comments}
      header={`${comments.length} ${comments.length > 1 ? "replies" : "reply"}`}
      itemLayout="horizontal"
      renderItem={(props) => (
        <Comment
          actions={[
            <span key="comment-basic-reply-to" onClick={() => replyTo(props)}>
              Reply to
            </span>,
          ]}
          {...props}
        >
          {
            <List
              locale={{ emptyText: "No comments" }}
              dataSource={props.child}
              itemLayout="horizontal"
              renderItem={(props) => {
                const temp1 = {
                  ...props,
                  id: props.id,
                  userId: props.userId,
                  child: props.child,
                  author: props.name + " reply " + props.toUserName,
                  avatar: props.imgUrl,
                  content: props.content,
                  datetime: props.createTime,
                };

                return (
                  <Comment
                    actions={[
                      <span
                        key="comment-basic-reply-to"
                        onClick={() => replyTo(temp1)}
                      >
                        Reply to
                      </span>,
                    ]}
                    {...temp1}
                  >
                    {}
                  </Comment>
                );
              }}
            />
          }
        </Comment>
      )}
    />
  );
  function replyTo(e) {
    onChange(e);
    // setReplyId();
  }
};
const Detail = memo(() => {
  const { id } = useParams();
  const [dataInfo, setDataInfo] = useState([]);

  const getInfo = async () => {
    try {
      if (id) {
        const res = await getPost(id);
        if (res.code === 200) {
          let temp = res.data;
          let templist = res.data.commentList.map((item) => {
            return {
              id: item.id,
              userId: item.userId,
              child: item.child,
              author: item.name,
              avatar: item.imgUrl,
              content: item.content,
              datetime: item.createTime,
            };
          });
          temp.commentList = templist;
          setDataInfo(temp);
        }
      }
    } catch (error) {
      console.log(error);
    }
  };

  const [comments, setComments] = useState([]);
  const [submitting, setSubmitting] = useState(false);
  const [value, setValue] = useState("");
  const [parentId, setParentId] = useState("");
  const [toId, setToId] = useState("");
  const handleSubmit = async () => {
    if (!value) return;
    setSubmitting(true);
    let param = {
      parentId: parentId,
      toId: toId,
      postId: id,
      content: value,
    };
    const res = await comment(param);
    if (res.code === 200) {
      message.success("successful!");
      setSubmitting(false);
      setParentId("");
      setValue("");
      getInfo();
    } else {
      message.error(res.message);
    }
  };
  const handleChange = (e) => {
    setValue(e.target.value);
  };

  const handleReplyChange = (e) => {
    setToId(e.userId);
    setParentId(e.parentId);
  };
  useEffect(() => {
    getInfo();
  }, []);
  return (
    <DetailWrapper>
      <div className="content">
        <div className="mainbox">
          <div className="topinfo">
            <div className="avatar">
              <Avatar size={55}>USER</Avatar>
            </div>
            <div className="contentbox">
              <div className="top">
                <div className="name">{dataInfo.name}</div>
              </div>
              <div>{dataInfo.createTime}</div>
            </div>
          </div>
          <div className="detail">
            <div className="content">{dataInfo.content}</div>
            <div className="imgbox">
              {dataInfo.images &&
                dataInfo.images.map((item, index) => {
                  return (
                    <Image
                      key={index}
                      width={280}
                      height={280}
                      src={item}
                      preview={item}
                    />
                  );
                })}
            </div>
            <Divider>comment</Divider>
            <div className="comment">
              <>
                {dataInfo.commentList && (
                  <CommentList
                    comments={dataInfo.commentList}
                    onChange={handleReplyChange}
                  />
                )}
                <Comment
                  content={
                    parentId ? (
                      <div>
                        <div style={{ marginBottom: "10px" }}>Reply to</div>
                        <Editor
                          onChange={handleChange}
                          onSubmit={handleSubmit}
                          submitting={submitting}
                          value={value}
                        />
                      </div>
                    ) : (
                      <div>
                        <Editor
                          onChange={handleChange}
                          onSubmit={handleSubmit}
                          submitting={submitting}
                          value={value}
                        />
                      </div>
                    )
                  }
                />
              </>
            </div>
          </div>
        </div>
      </div>
    </DetailWrapper>
  );
});

export default Detail;
